.header {
    padding: 16px 0;
    position: sticky;
    top: 0;
    left: 0;
    width: 100%;
    background-color: $primary-brand;
    z-index: 1111;
    @include desktop-down{
      padding: 23px 0;
    }
    &.fixed{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      z-index: 11111;
    }
    li{
      color: $white;
      font-weight: 500;
      position: relative;
      a{
        text-decoration: none;
        letter-spacing: 0.8px;
        color: inherit;
        padding: 23px 7px;
        position: relative;
        transition: all ease-out 0.2s;
      }

      &.selected {
        a{
          &::after{
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            width: calc(100%);
            height: 7px;
            background-color: $primary-l1;
            @include desktop-down{
              display: none;
            }
          }
        }
      }
    }

    .header__menu-list{
      justify-content: center;
    }
  }

.header__column-wrapper,
.header__col-seven,
.header__menu-list {
  position: relative;
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}
.header__col-sevedn--left-menu {
  width: 112px;
  @include desktop-down {
    width: 100%;
    text-align: left;
    margin: 117px 0 20px;
  }
  img {
    width: 100%;
  }
}
.header__column-wrapper {
  padding: 0;
  @include desktop-down {
    padding-bottom: 10px;
    &.active {
      .header__logo,
      .header__bar-wrapper,
      .header__col-three {
        display: none;
      }
      .header__mobile-menu {
        width: calc(100% + 40px);
        left: -20px;
        background: $gray 0% 0% no-repeat padding-box;
        border-width: 1px;
      }
      .close {
        display: flex;
      }
      .header__col-seven {
        width: calc(100% + 40px);
        padding: 0 20px;
      }
    }
  }
}
.header__mobile-menu {
  display: none;
  @include desktop-down {
    display: flex;
    align-items: center;
    position: absolute;
    width: calc(100% - 80px);
    height: 56px;
    top: 50%;
    left: 0;
    border-bottom: 0px solid $gray-l1;
    transform: translateY(-50%);
    z-index: 1;
    h3 {
      font-size: $font-size-25;
    }

    .bar-wrapper {
      display: flex;
      align-items: center;
      height: 20px;
    }

    svg {
      width: 28px;
      margin: 0 10px 0 0;
      cursor: pointer;
    }

    .close {
      display: none;
      flex-wrap: wrap;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      padding-left: 20px;
      font-weight: 600;
    }

    .header__logo {
      position: relative;
      left: 50%;
      transform: translateX(-50%);
      height: 30px;
      a{
        height: 100%;
      }
      img{
        width: 100%;
        height: 100%;
      }
    }
  }
}
.header__col-seven {
  width: 65.5%;
  @include desktop {
    width: 66.5%;
  }
  @include desktop-down {
    flex-direction: column-reverse;
    width: 0;
    position: absolute;
    top: 40px;
    left: -20px;
    overflow: hidden;
    .menu-title {
      display: none;
    }
  }
}
.header__col-seven--center-menu {
  width: calc(100% - 140px);
  text-align: center;
  @include desktop-down {
    width: 100%;
    text-align: left;
  }
    display: block;
    .header__menu-list {
      justify-content: flex-end;

      @include desktop-down {
        display: block;
      }
      li {
        margin-right: 18px;
        font-size: $font-size-16;
        @include desktop-down {
          padding: 16px 0;
          border-bottom: 2px solid $gray-l1;
          color: $unnamed-color-172026;
          &:last-child {
            margin-bottom: 0;
          }
          a {
            padding: 0;
            line-height: $line-spacing-24;
          }
        }
        &.selected {
          @include desktop-down {
            border-color: $primary-brand;
          }
        }
        &:last-child {
          margin-right: 0;
        }
        @include desktop-md-down {
          margin-right: 12px;
        }
      }
    }
  
}
.header__col-three {
  width: 30%;
  @include desktop-down {
    margin-right: 0;
    position: absolute;
    width: auto;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    z-index: 1111;
  }
  .header__menu-list {
    justify-content: flex-end;
    li {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      margin: 0 25px 0 0;
      cursor: pointer;
      &:last-child {
        margin-right: 0;
      }
      span {
        padding: 0 0 0 8px;
        @include desktop-down {
          display: none;
        }
      }
      svg {
        width: 23px;
        height: 24px;
      }
      &#cartBtn {
        span {
          display: inline-flex;
          align-items: center;
          justify-content: center;
          width: 23px;
          height: 23px;
          padding: 0;
          position: relative;
          top: -11px;
          left: -14px;
          color: $white;
          background-color: $primary-m1;
          border-radius: 50%;
        }
      }
    }
  }
}
